$mainColor: #89ddf4;

@font-face {
    font-family: Breaking;
    src: url(/src/fonts/Breaking.ttf);
}

@font-face {
    font-family: MorningBread;
    src: url(/src/fonts/MorningBread-8Ml7A.ttf);
}

@font-face {
    font-family: Cream;
    src: url(/src/fonts/Cream.ttf);
}

#main-nav {
    display: flex;
    flex-direction: column;
    margin: 0px;
    align-items: center;
    position: relative;
}

.navigation {
    display: flex;
    width: 1200px;
    height: 50px;
}

.nav-bar {
    display: flex;
    width: 1200px;
    justify-content: space-evenly;
    padding: 0px;
    margin: 0px;
}

.nav-bar li {
    display: inline;
    list-style-type: none;
    font-family: Breaking;
    font-size: 25px;
    color: rgb(83, 125, 159);
    padding-left: 20px;
    padding-right: 20px;
    padding-top: 10px;
    
}

.nav-bar li:hover {
    text-shadow:1px 1px white;
    background-color: rgb(173, 216, 230, 0.4);
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
}

.main-bg {
    display: flex;
    position: relative;
    align-self: center;
    border-radius: 35px;
    background-color: rgb(173, 216, 230, 0.4);
    width: 1300px;
    height: 700px;
    z-index: 0;
    justify-content: space-evenly;
}

.data-sections-left {
    display: grid;
    // background-color: black;
    border-radius: 35px;
    align-content: space-evenly;
}

.size-comp {
    width: 750px;
    height: 350px;
    // background-color: green;
    border-radius: 20px;
    margin-top: 30px;
    margin-left: 30px;
    margin-right: 15px;
    margin-bottom: 15px;
    padding: 30px;
    font-family: Cream;
    font-size: 25px;
}

.population-graph {
    width: 750px;
    height: 124px;
    // background-color: pink;
    border-radius: 20px;
    margin-top: 15px;
    margin-left: 30px;
    margin-bottom: 30px;
    margin-right: 15px;
    padding: 30px;
    font-family: Cream;
    font-size: 25px;
    display: flex;
    align-items: flex-start;
}


.data-sections-right {
    display: grid;
    // background-color: red;
    border-radius: 35px;
    align-content: space-evenly;
}

.basic-facts {
    width: 350px;
    height: 350;
    // background-color: yellow;
    border-radius: 20px;
    margin-top: 10px;
    margin-left: 15px;
    padding-left: 10px;
    padding-right: 10px;
    overflow: scroll;
    text-align: center;
}

.fact-list {
    font-family: MorningBread;
    font-size: 25px;
    text-align: left;
}


.labels {
    font-family: Cream;
    font-size: 25;
    text-align: center;
}


.fact-list span {
    font-family: Cream;
    font-size: 18;
}

.range-map {
    width: 350px;
    height: 200px;
    // background-color: pink;
    border-radius: 20px;
    margin-top: 0px;
    margin-left: 15px;
    margin-bottom: 30px;
    margin-right: 30px;
    padding-left: 10px;
    padding-right: 10px;
    font-family: Cream;
    font-size: 25px;
    text-align: center;
}

.range-map img {
    width: -webkit-fill-available;
}





